import React from "react";

import { Row, Column } from "components/Grid.tsx";
import { filingDisputeMessages } from "../messages";
import {
  FilingSection,
  FilingTitle,
  FilingSubtitle,
  DetailsSection,
  Section,
  Image,
  Number,
  Title,
  Description,
} from "./elements";

const FilingDispute = () => (
  <FilingSection>
    <FilingTitle>{filingDisputeMessages.title}</FilingTitle>
    <FilingSubtitle>{filingDisputeMessages.subtitle}</FilingSubtitle>
    <DetailsSection>
      {filingDisputeMessages.sections.map((section, index) => (
        <Section key={section.title}>
          <Row
            reversedFlexDirection={index % 2 === 0}
            padding={30}
            paddingWidth={26}
            verticalAlignment="center"
            horizontalAlignment="space-between"
          >
            <Column span={6} spanMedium={5} spanSmall={12}>
              <Image src={section.img} alt={section.title} />
              <Number>{`0${index + 1}`}</Number>
            </Column>
            <Column span={5} spanMedium={6} spanSmall={12}>
              <Title>{section.title}</Title>
              <Description>{section.description}</Description>
            </Column>
          </Row>
        </Section>
      ))}
    </DetailsSection>
  </FilingSection>
);

export default FilingDispute;
